import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup,  Validators } from '@angular/forms';
import { SharedService } from  '../../service/shared.service';
import { AlertService } from  '../../service/alert.service';
import { AuthService } from '../../service/auth.service';

@Component({
	selector: 'app-site-setting',
	templateUrl: './site-setting.component.html',
	styleUrls: ['./site-setting.component.scss']
})
export class SiteSettingComponent implements OnInit {
	form: FormGroup;
	isLoading = true;
	submitted = false;
	user :any;
	constructor(private formBuilder: FormBuilder,
		private sharedService: SharedService, 
		private alertService:AlertService,
		private authService:AuthService) { }

	async ngOnInit() {
		//this.existEmailError='';
		const apiData=this.sharedService.getApiData('profile');

		if(apiData){
			this.user=apiData;
			this.form = this.formBuilder.group({
				email: [this.user.email, Validators.compose([Validators.required, Validators.email,Validators.pattern(this.authService.EMAIL_REGEXP)])],
				password: ['', [Validators.pattern('^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$')]],
				oAuthEncryption:['']
			});
		}
		this.isLoading=this.sharedService.checkIfApiNotCalled('profile');
		let data = await this.authService.me().catch(e => console.log('Error: ', e.message));
		if(!data) return;
		this.isLoading=false;
		this.sharedService.setApiCalled('profile',data.data);
		this.user=data.data;
		this.form = this.formBuilder.group({
			email: [this.user.email, Validators.compose([Validators.required, Validators.email,Validators.pattern(this.authService.EMAIL_REGEXP)])],
			password: ['', [Validators.pattern('^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$')]],
			oAuthEncryption:['']
		});
	}

	get f() { return this.form.controls; }

	onSubmit() {
		this.submitted = true;
		if (this.form.invalid) {
			return;
		}
		let postData={email:this.f.email.value,password:this.f.password.value,oAuthEncryption:this.f.oAuthEncryption.value}
		this.sharedService.postData('user/update-profile',postData).subscribe(success => {
			if(success.is_success){
				this.alertService.success('Setting saved successfully.',{});
				this.user=success.data;
				this.sharedService.setApiCalled('profile',success.data);

			}else{
				let message=success.error.message;
				this.alertService.error(message,{});
			}			
		},error=>{
			console.error(error);

		});
	}

}
