import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup,FormControl,  Validators,AbstractControl,ValidatorFn } from '@angular/forms';
import { SharedService } from  '../../service/shared.service';
import { AlertService } from  '../../service/alert.service';

@Component({
	selector: 'app-product',
	templateUrl: './product.component.html',
	styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit {

	//form: FormGroup;
	isLoading = false;
	submitted = false;
	product :any;

	form = new FormGroup({
		// 'productId': new FormControl(),
		'free': new FormControl(),
		'first': new FormGroup({
			'planValue': new FormControl(),
			'planChargePerRequest': new FormControl()
		}),
		'second': new FormGroup({
			'planValue': new FormControl(),
			'planChargePerRequest': new FormControl()
		}),
		'third': new FormGroup({
			'planValue': new FormControl(),
			'planChargePerRequest': new FormControl()
		}),
		'unlimited': new FormGroup({
			'planValue': new FormControl(),
			'planChargePerRequest': new FormControl()
		})		
	});

	constructor( private formBuilder:FormBuilder,private sharedService:SharedService,private alertService:AlertService) { }

	async ngOnInit() {
		const NUMBER_REGEXP = "^[0-9]*$";
		const AMOUNT_REGEXP = "^-?[0-9]\\d*(\\.\\d{1,6})?$";
		const apiData=this.sharedService.getApiData('product');
		if(apiData){
			this.product=apiData;
			if(this.product && this.product.pricePlan){
				const productDetail=this.product.pricePlan;
				var formGroupObject={};

				productDetail.forEach((item,i)=>{
					if(productDetail[i].planName=='free'){
						formGroupObject[productDetail[i].planName]=[productDetail[i].planValue, Validators.compose([Validators.required,Validators.pattern(NUMBER_REGEXP)])]
					}else{
						formGroupObject[productDetail[i].planName]=this.formBuilder.group({
							planValue: [productDetail[i].planValue, Validators.compose([Validators.required, Validators.pattern(NUMBER_REGEXP),this.customValidatorCompareValue(productDetail[i-1].planName)])],
							planChargePerRequest: [productDetail[i].planChargePerRequest,Validators.compose([Validators.required, Validators.pattern(AMOUNT_REGEXP)])]
						})
					}

				}) 
				this.form = this.formBuilder.group(formGroupObject);
			}
		}
		this.isLoading=this.sharedService.checkIfApiNotCalled('product');
		this.product=await this.getProduct();
		this.sharedService.setApiCalled('product',this.product);
		this.isLoading=false;

		
		if(this.product && this.product.pricePlan){
			const productDetail=this.product.pricePlan;
			var formGroupObject={};

			productDetail.forEach((item,i)=>{
				if(productDetail[i].planName=='free'){
					formGroupObject[productDetail[i].planName]=[productDetail[i].planValue, Validators.compose([Validators.required,Validators.pattern(NUMBER_REGEXP)])]
				}else{
					formGroupObject[productDetail[i].planName]=this.formBuilder.group({
						planValue: [productDetail[i].planValue, Validators.compose([Validators.required, Validators.pattern(NUMBER_REGEXP),this.customValidatorCompareValue(productDetail[i-1].planName)])],
						planChargePerRequest: [productDetail[i].planChargePerRequest,Validators.compose([Validators.required, Validators.pattern(AMOUNT_REGEXP)])]
					})
				}

			}) 
			this.form = this.formBuilder.group(formGroupObject);

		}else{

			this.form = this.formBuilder.group({
				free: ['', Validators.compose([Validators.required,Validators.pattern(NUMBER_REGEXP)])],
				first: this.formBuilder.group({
					planValue: ['', Validators.compose([Validators.required, Validators.pattern(NUMBER_REGEXP),this.customValidatorCompareValue("free")])],
					planChargePerRequest: ['',Validators.compose([Validators.required, Validators.pattern(AMOUNT_REGEXP)])]
				}),
				second: this.formBuilder.group({
					planValue: ['', Validators.compose([Validators.required, Validators.pattern(NUMBER_REGEXP),this.customValidatorCompareValue("first")])],
					planChargePerRequest: ['',Validators.compose([Validators.required, Validators.pattern(AMOUNT_REGEXP)])]
				}),
				third: this.formBuilder.group({
					planValue: ['', Validators.compose([Validators.required, Validators.pattern(NUMBER_REGEXP),this.customValidatorCompareValue("second")])],
					planChargePerRequest: ['',Validators.compose([Validators.required, Validators.pattern(AMOUNT_REGEXP)])]
				}),
				unlimited: this.formBuilder.group({
					planValue: ['', Validators.compose([Validators.required, Validators.pattern(NUMBER_REGEXP),this.customValidatorCompareValue("third")])],
					planChargePerRequest: ['',Validators.compose([Validators.required, Validators.pattern(AMOUNT_REGEXP)])]
				})

			});
		}

	}

	customValidatorGroup(group){
	}

	customValidatorCompareValue(groupName):ValidatorFn{
		let returnValue=null;
		return (c: AbstractControl): { [key: string]: any | null } => {
			if (!c.root || !c.parent) {
				return null;
			}
			const parentForm=c.root ;
			const previousValue=(groupName=='free')?parentForm.get(groupName).value:parentForm.get(groupName).get('planValue').value;

			returnValue=(parseInt(previousValue)<parseInt(c.value))?null:{notGreater:true};
			return returnValue;
		}

	}

	get f() { 
		return this.form.controls; 
	}

	async getProduct(){

		const promise1 = await this.sharedService.getData('product/detail').toPromise();
		return promise1.data;
	}


	onSubmit(){
		this.submitted=true;
		if (this.form.invalid) {
			return;
		}

		let bodyData=this.form.value;
		if(this.product && this.product._id){
			bodyData.productId=this.product._id;
		}

		this.sharedService.postData('product/update',bodyData).subscribe(success => {

			if(success.is_success){
				this.alertService.success('Product saved successfully')
			}

		},error=>{

		});	
	}

}
