import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminComponent } from './admin.component';
import { HomeComponent } from './home/home.component';
import { AuthGuard } from '../_helpers';
import { ProductComponent } from './product/product.component';
import { UserComponent } from './user/user.component';
import { SiteSettingComponent } from './site-setting/site-setting.component';


const routes: Routes = [
  {
    path: '', component: AdminComponent, children: [
      {
        path: '', component: HomeComponent, canActivate: [AuthGuard], data: {
          title: 'J U P I T A | Admin Panel Home'
        }
      },
      {
        path: 'products', component: ProductComponent,
        data: {
          title: 'J U P I T A | User Products'
        }
      },
      {
        path: 'users', component: UserComponent,
        data: {
          title: 'J U P I T A | Users'
        }
      },
      {
        path: 'site-setting', component: SiteSettingComponent,
        data: {
          title: 'J U P I T A | Site Settings'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
